
body{position:static
}
.ta-g{position:relative
}
.ta-loader{position:absolute;top:0;bottom:0;z-index:1300;width:100%;overflow-x:hidden;background-color:#2f3638;box-sizing:border-box;display:-ms-flexbox;display:flex;-ms-flex:0 1 auto;flex:0 1 auto;-ms-flex-direction:column;flex-direction:column;-ms-flex-positive:1;flex-grow:1;-ms-flex-negative:0;flex-shrink:0;-ms-flex-preferred-size:25%;flex-basis:25%;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
